                  <option v-for="weapon in genshinWeapon" :key="weapon.id" :value="weapon.name">
                    {{ weapon.name }}
                  </option>
                </select>
                <div v-if="newWeapon.name">
                  <div>
                    <div class="level-control">
                      <label for="LevelRange">Уровень таланта:</label>
                      <input type="range" min="1" max="90" v-model="newWeapon.Level" id="LevelRange" class="level-range">
                      <span>{{ newWeapon.Level }}</span>
                    </div>
                  </div>
                  <button class="add-button" type="submit" @click="addWeapon">Добавить</button>
                </div>
              </div>
            </form>
          </div>
        </div>
        <table v-if="weapons.length > 0">
          <tbody class="pers">
          <tr v-for="(weapon, index) in weapons" :key="index" :class="{ 'favorite': weapon.favorite }">
            <td>
              <img :src="weapon.imagePath" :alt="weapon.name" class="character-image">
            </td>
            <td>
              <tr>
                <span class="character-name">{{ weapon.name }}</span>
                <button class="favorite-button" @click="toggleFavorite_weapon(weapon)">
                  ❤️
                </button>
              </tr>
              <tr>
                <div class="level-up-days">
                  <p class="level-up-heading">Дни повышения уровня:</p><p class="level-up-days_2"> {{ weapon.result }}</p>
                </div>
              </tr>
            </td>
            <td class="levels">
              <tr>
                <p class="character-stat">Уровень оружия: {{ weapon.Level }}</p>
                <div class="level-control">
                  <button @click="decreaseLevel_weapon(weapon, 'Level')" class="level-button_1"><</button>
                  <div class="level-bar-container">
                    <div class="level-bar_1" :style="{ width: (weapon.Level * 1.11) + '%' }"></div>
                  </div>
                  <button @click="increaseLevel_weapon(weapon, 'Level')" class="level-button_1">></button>
                </div>
              </tr>
            </td>
          </tr>
          </tbody>
        </table>
      </div>

    </div>
    <div id="app_1">
      <footer class="link">
        <a class="link" href="/" v-on:click.prevent="logoutRequest"><img  src="./public/icon.png" alt="About Page"></a>
      </footer>
    </div>
  </div>
</template>

<script>
export default {
  name: "Main",
  data() {
    return {
      action: 'main',
      selectedCharacter: '',
      selectedWeapon: '',
      name: '',
      firstLevel: 1,
      secondLevel: 1,
      thirdLevel: 1,
      newCharacter: {
        name: 'F',
        firstLevel: 1,
        secondLevel: 1,
        thirdLevel: 1,
        imagePath: 'https://i.pinimg.com/originals/cb/6b/57/cb6b57826dc61b9f419ad29ddceb9b2f.jpg'
      },
      newWeapon: {
        name: 'F',
        typeOfWeapon: '',
        Level: 1,
        imagePath: 'https://i.pinimg.com/originals/cb/6b/57/cb6b57826dc61b9f419ad29ddceb9b2f.jpg'
      },
      characters: [],
      weapons: [],
      showForm: false,
      showForm_weapon: false,
      genshinCharacters: [
        { id: 1, name: 'Amber', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-ambor.png'},
        { id: 2, name: 'Barbara', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-barbara.png'},
        { id: 3, name: 'Beidou', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-beidou.png'},
        { id: 4, name: 'Bennett', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-bennett.png'},
        { id: 5, name: 'Chongyun', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-chongyun.png'},
        { id: 6, name: 'Diluc', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-diluc.png'},
        { id: 7, name: 'Diona', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-diona.png'},
        { id: 8, name: 'Eula', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-eula.png'},
        { id: 9, name: 'Fischl', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-fischl.png'},
        { id: 10, name: 'Ganyu', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-ganyu.png'},
        { id: 11, name: 'Hu Tao', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-hutao.png'},
        { id: 12, name: 'Jean', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-qin.png'},
        { id: 13, name: 'Kaeya', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-kaeya.png'},
        { id: 14, name: 'Klee', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-klee.png'},
        { id: 15, name: 'Lisa', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-lisa.png'},
        { id: 16, name: 'Mona', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-mona.png'},
        { id: 17, name: 'Ningguang', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-ningguang.png'},
        { id: 18, name: 'Noelle', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-noel.png'},
        { id: 19, name: 'Qiqi', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-qiqi.png'},
        { id: 20, name: 'Razor', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-razor.png'}
      ],
      genshinWeapon: [
        { id: 1, name: 'Aqua Simulacra', typeOfWeapon: 'Лук',imagePath:'https://genshin.guru/wp-content/uploads/2022/07/akva-simulyakrum-optimized.png'},
        { id: 2, name: 'Beacon of the Reed Sea',typeOfWeapon: 'Лук', imagePath:'https://static.wikia.nocookie.net/gensin-impact/images/6/6c/Weapon_Beacon_of_the_Reed_Sea.png/revision/latest/scale-to-width-down/60?cb=20231219232124'},
        { id: 3, name: 'Beidou', typeOfWeapon: 'Лук', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-beidou.png'},
        { id: 4, name: 'Bennett', typeOfWeapon: 'Лук', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-bennett.png'},
        { id: 5, name: 'Chongyun', typeOfWeapon: 'Лук', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-chongyun.png'},
        { id: 6, name: 'Diluc', typeOfWeapon: 'Лук', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-diluc.png'},
        { id: 7, name: 'Diona', typeOfWeapon: 'Лук', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-diona.png'},
        { id: 8, name: 'Eula', typeOfWeapon: 'Лук', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-eula.png'},
        { id: 9, name: 'Fischl', typeOfWeapon: 'Лук', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-fischl.png'},
        { id: 10, name: 'Ganyu', typeOfWeapon: 'Лук', imagePath:'https://cdn.wanderer.moe/genshin-impact/character-icons/ui-avataricon-ganyu.png'}
      ]
    };
  },
  created() {
    this.$axios({
      method: 'get',
      url:  this.$BaseURL + 'points',
    }).then(response => {

          // Обновление данных в таблице
      const updatedCharacters = response.data.map(character => {
        const foundCharacter = this.genshinCharacters.find(char => char.name === character.name);
        if (foundCharacter) {
          character.imagePath = foundCharacter.imagePath;
        }
        return character;
      });
      this.characters = updatedCharacters;
    }).catch(error => {
          console.log(error);
    });
  },


  methods: {
    addCharacter() {
      if (this.newCharacter.name) {
        // Проверяем, что персонаж с таким именем еще не выбран
        const isCharacterAlreadySelected = this.characters.some(char => char.name === this.newCharacter.name);
        if (!isCharacterAlreadySelected) {
          const selectedCharacter = this.genshinCharacters.find(char => char.name === this.newCharacter.name);
          if (selectedCharacter) {
            this.characters.push({
              name: this.newCharacter.name,
              firstLevel: this.newCharacter.firstLevel,
              secondLevel: this.newCharacter.secondLevel,
              thirdLevel: this.newCharacter.thirdLevel,
              imagePath: selectedCharacter.imagePath,
              levelUpDays: this.newCharacter.levelUpDays,
            });
            console.log(this.newCharacter.name, this.newCharacter.firstLevel, this.newCharacter.secondLevel, this.newCharacter.thirdLevel);
            this.addPoint();
            this.resetForm();
            this.toggleForm(); // Скрыть форму после добавления
          } else {
            console.log('Выбранный персонаж не найден');
          }
        } else {
          console.log('Этот персонаж уже выбран');
          alert('Этот персонаж уже выбран');
        }
      }
    },
    addWeapon() {
      if (this.newWeapon.name) {
        // Проверяем, что персонаж с таким именем еще не выбран
        const isWeaponAlreadySelected = this.weapons.some(char => char.name === this.newWeapon.name);
        if (!isWeaponAlreadySelected) {
          const selectedWeapon = this.genshinWeapon.find(char => char.name === this.newWeapon.name);
          if (selectedWeapon) {
            this.weapons.push({
              name: this.newWeapon.name,
              Level: this.newWeapon.Level,
              imagePath: selectedWeapon.imagePath
            });
            console.log(this.newCharacter.name, this.newCharacter.firstLevel, this.newCharacter.secondLevel, this.newCharacter.thirdLevel);
            this.addPoint_weapon();
            this.resetForm_weapon();
            this.toggleForm_weapon(); // Скрыть форму после добавления
          } else {
            console.log('Выбранный персонаж не найден');
          }
        } else {
          console.log('Этот персонаж уже выбран');
          alert('Этот персонаж уже выбран');
        }
      }
    },
    resetForm() {
      this.newCharacter = {
        name: '',
        firstLevel: 1,
        secondLevel: 1,
        thirdLevel: 1,
        levelUpDays: ''
      };
    },
    resetForm_weapon() {
      this.newWeapon = {
        name: '',
        Level: 1,
      };
    },
    toggleForm() {
      this.showForm = !this.showForm; // Показать/скрыть форму
    },
    toggleForm_weapon() {
      this.showForm_weapon = !this.showForm_weapon; // Показать/скрыть форму
    },
    getCharacterImage(characterName) {
      // Используем путь к изображению, хранимый в genshinCharacters
      const selectedCharacter = this.genshinCharacters.find(char => char.name === characterName);
      if (selectedCharacter) {
        return selectedCharacter.imagePath;
      }
      // Вернуть путь к заглушке, если путь к изображению не найден
      return '/placeholder-image.jpg';
    },
    increaseLevel(character, levelType) {
      if (character[levelType] < 90) {
        character[levelType]++;
        this.addPointRequest(character.name, character.firstLevel, character.secondLevel, character.thirdLevel, character.levelUpDays, character.imagePath)
      }
    },
    increaseLevel_weapon(weapon, levelType) {
      if (weapon[levelType] < 90) {
        weapon[levelType]++;
        this.addPointRequest(weapon.name, weapon.Level, weapon.typeOfWeapon, weapon.imagePath)
      }
    },
    decreaseLevel(character, levelType) {
      if (character[levelType] > 1) {
        character[levelType]--;
        // this.updateCharacterOnServer(character); // Вызываем метод обновления данных на сервере
        this.addPointRequest(character.name, character.firstLevel, character.secondLevel, character.thirdLevel, character.levelUpDays, character.imagePath)
      }
    },
    decreaseLevel_weapon(weapon, levelType) {
      if (weapon[levelType] > 1) {
        weapon[levelType]--;
        // this.updateCharacterOnServer(character); // Вызываем метод обновления данных на сервере
        this.addPointRequest(weapon.name, weapon.Level, weapon.typeOfWeapon, weapon.imagePath)
      }
    },
    toggleFavorite(character) {
      character.favorite = !character.favorite;

      if (character.favorite) {
        // Если персонаж добавлен в избранное, переместим его в начало массива
        const index = this.characters.indexOf(character);
        if (index !== -1) {
          this.characters.splice(index, 1);
          this.characters.unshift(character);
        }
      } else {
        // Если персонаж удален из избранного, переместим его обратно в изначальное положение в массиве
        const index = this.characters.indexOf(character);
        if (index !== -1) {
          this.characters.splice(index, 1);
          this.characters.push(character);
        }
      }
    },
    toggleFavorite_weapon(weapon) {
      weapon.favorite = !weapon.favorite;

      if (weapon.favorite) {
        // Если персонаж добавлен в избранное, переместим его в начало массива
        const index = this.weapons.indexOf(weapon);
        if (index !== -1) {
          this.weapons.splice(index, 1);
          this.weapons.unshift(weapon);
        }
      } else {
        // Если персонаж удален из избранного, переместим его обратно в изначальное положение в массиве
        const index = this.weapons.indexOf(weapon);
        if (index !== -1) {
          this.weapons.splice(index, 1);
          this.weapons.push(weapon);
        }
      }
    },
    logoutRequest: function () {
      this.$axios({
        method: 'post',
        url: this.$BaseURL + 'logout',
      }).then(response => {
        this.response = response;
        localStorage.removeItem('currentUser');
        this.$router.push('/');
      }).catch(error => {
        console.log(error);
      });
    },
    addPointRequest: function (name, firstLevel, secondLevel, thirdLevel, levelUpDays, imagePath) {
      let base64Credential = localStorage.getItem('currentUser');
      let username = localStorage.getItem('username');
      console.log(name, firstLevel, secondLevel, thirdLevel, imagePath);
      return this.$axios({
        method: 'post',
        url: this.$BaseURL + 'points',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': base64Credential
        },
        data: {name: name, firstLevel: firstLevel, secondLevel: secondLevel, thirdLevel: thirdLevel, levelUpDays: levelUpDays, imagePath: imagePath }
      }).then(response => {

        // console.log(response);
        let result = response.data.result;
        const existingCharacterIndex = this.characters.findIndex(char => char.name === name);
        const selectedCharacter = this.genshinCharacters.find(char => char.name === name); // Находим соответствующий объект-персонаж
        if (existingCharacterIndex !== -1) {
          // Update existing character if found
          this.$set(this.characters, existingCharacterIndex, {name, firstLevel, secondLevel, thirdLevel, result, imagePath: selectedCharacter ? selectedCharacter.imagePath : 'https://i.pinimg.com/originals/cb/6b/57/cb6b57826dc61b9f419ad29ddceb9b2f.jpg' , username});
          console.log(this.characters)
          // localStorage.setItem('characters', JSON.stringify(this.characters));
        } else {
          // Add new character if not found
          // this.characters.unshift({name, firstLevel, secondLevel, thirdLevel, result});
          this.characters.unshift({name, firstLevel, secondLevel, thirdLevel, result, imagePath: selectedCharacter ? selectedCharacter.imagePath : 'https://i.pinimg.com/originals/cb/6b/57/cb6b57826dc61b9f419ad29ddceb9b2f.jpg' });
        }
        // this.characters.unshift({name: name, firstLevel: firstLevel, secondLevel: secondLevel, thirdLevel: thirdLevel, result: result});
        this.response = response;
        console.log(response);

      }).catch(error => {
        console.log(error.response.data);
        return false;
      });
    },
    addPointRequest_weapon: function (name, Level, typeOfWeapon, imagePath) {
      let base64Credential = localStorage.getItem('currentUser');
      let username = localStorage.getItem('username');
      return this.$axios({
        method: 'post',
        url: this.$BaseURL + 'weapon',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': base64Credential
        },
        data: {name: name, level: Level, typeOfWeapon: typeOfWeapon, imagePath: imagePath}
      }).then(response => {
        let result = response.data.result;
        const existingCharacterIndex = this.weapons.findIndex(char => char.name === name);
        const selectedCharacter = this.genshinWeapon.find(char => char.name === name); // Находим соответствующий объект-персонаж
        if (existingCharacterIndex !== -1) {
          // Update existing character if found
          this.$set(this.weapons, existingCharacterIndex, {name, Level, typeOfWeapon, result, imagePath: selectedCharacter ? selectedCharacter.imagePath : 'https://i.pinimg.com/originals/cb/6b/57/cb6b57826dc61b9f419ad29ddceb9b2f.jpg'});
          console.log(this.weapons)
          // localStorage.setItem('characters', JSON.stringify(this.characters));
        } else {
          // Add new character if not found
          // this.characters.unshift({name, firstLevel, secondLevel, thirdLevel, result});
          this.weapons.unshift({name, Level, typeOfWeapon, result, imagePath: selectedCharacter ? selectedCharacter.imagePath : 'https://i.pinimg.com/originals/cb/6b/57/cb6b57826dc61b9f419ad29ddceb9b2f.jpg'});
        }
        // this.characters.unshift({name: name, firstLevel: firstLevel, secondLevel: secondLevel, thirdLevel: thirdLevel, result: result});
        this.response = response;
        console.log(response);

      }).catch(error => {
        console.log(error.response.data);
        return false;
      });
    },
    addPoint: function (){
      if (this.newCharacter.name && this.newCharacter.firstLevel && this.newCharacter.secondLevel && this.newCharacter.thirdLevel) {
        this.addPointRequest(this.newCharacter.name, this.newCharacter.firstLevel, this.newCharacter.secondLevel, this.newCharacter.thirdLevel, this.newCharacter.imagePath);
        console.log(this.newCharacter.name, this.newCharacter.firstLevel, this.newCharacter.secondLevel, this.newCharacter.thirdLevel)}
      else{
        console.log('Некоторые или все данные не заполнены');
        console.log(this.newCharacter.name, this.newCharacter.firstLevel, this.newCharacter.secondLevel, this.newCharacter.thirdLevel);
      }
    },
    addPoint_weapon: function (){
      if (this.newWeapon.name && this.newWeapon.Level) {
        this.addPointRequest_weapon(this.newWeapon.name, this.newWeapon.Level, this.newWeapon.typeOfWeapon, this.newWeapon.imagePath);
        console.log(this.newWeapon.name, this.newWeapon.Level, this.newWeapon.typeOfWeapon, this.newWeapon.imagePath)}
      else{
        console.log('Некоторые или все данные не заполнены');
